body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Poppins', sans-serif;
}


.container {
    display: flex;
    flex-direction: column;
}

.featureContainer {
    background-color: #B10058;
    height: 600px;
    width: 100%;
    color: white;
}

.featurePhoto {
    height: 382px;
    width: 480px;
    margin-top: 100px;
    margin-right: 80px;
}

.featureParagraph {
    padding-top: 150px;
}

.superLargeText {
    font-size: 52px;
    margin-bottom: 20px;
    font-weight: 500;
}

.largeText {
    font-size: 36px;
    margin-bottom: 32px;
    font-weight: bold;
   
}

.headLine {
    font-size: 24px;
    margin-bottom: 12px;
    font-weight: bold;
    color: white;
    text-decoration: none;

}

a .headLine {
    text-decoration: none;
    color:white;
}
a .headLine :hover {
    text-decoration: none;
    text-decoration: white;
    color: white;
}

.normalText {
    font-size: 24px;

    font-weight: 200;
}

.productCard {
     display: block;
     justify-content: center;
     align-items: center
}

.row .productCard {
    width: 280px;
    height: 420px;
    background-color: white;
    margin-top: 12px;
    margin-left: 12px;
    margin-bottom: 12px;
    margin-right: 12px;

    border-radius: 10px;
    padding-top: 16px;
    padding-left: 16px;
    padding-right: 16px;
    padding-bottom: 30px;
     box-shadow: 0px 1px 5px #aaaaaa;;

    /* align-content: center;
    position: relative; */
}


.productCardImage {
    width: 240px;
    height: 240px;
    padding-bottom: 20px;
    object-fit: cover;
    display: block;
    border-radius: 10px;
}

.productCardTitle {
    height: 58px;
    padding-top: 4px;
    font-size: 18px;
    padding-bottom: 16px;
    font-family: 'Poppins', sans-serif;
    max-lines: 2;
    overflow: hidden;
}

.productCardPrice {
    width: 240px;
    padding-top: 20px;
    font-size: 22px;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    color: #B10058;
    text-align: right;

}


.productListContainer {
   min-height: 700px;
   align-items: center;
    margin-right: 50px;
    margin-left: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}